<template>
  <v-simple-table dense fixed-header>
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-center">Num</th>
          <th class="text-center">Freq.</th>
          <th class="text-center">Ac.Freq.</th>
          <th class="text-center">Prob.</th>
          <th class="text-center">Ac.Prob</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in tabledata" :key="item.name">
          <td>{{ item.number }}</td>
          <td>{{ item.freq }}</td>
          <td>{{ item.freqac }}</td>
          <td>{{ item.prob }}</td>
          <td>{{ item.probac }}</td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</template>

<script>
export default {
  props: ["info", "update"],
  watch: {
    update: function () {
      if (this.update[this.update.length - 1] == true) {
        this.sync();
      }
    },
  },
  data: function () {
    return {
      tabledata: [],
    };
  },
  methods: {
    sync: function () {
      this.tabledata=[];
      for (let i = 0; i < this.info.size; i++) {
        let p = {
          number: this.info.labels[i],
          freq: this.info.freqArray[i],
          freqac: this.info.freqacArray[i],
          prob: this.info.probArray[i],
          probac: this.info.probacArray[i],
        };
        this.tabledata.push(p);
      }
    },
  },
  mounted: function(){
    this.sync();
  }
};
</script>
<style>
table th + th {
  border-left: 1px solid #dddddd;
}
table td + td {
  border-left: 1px solid #dddddd;
}
</style>