<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  props: ["info", "update"],
  watch: {
    update: function () {
      if (this.update[this.update.length - 1] == true) {
        this.sync();
        this.renderChart(this.chartData, this.options);
      }
    },
  },
  data: function () {
    return {
      color1: "rgba(75, 192, 192, 0.2)",
      color2: "rgba(75, 192, 192, 1)",
      chartData: {
        labels: null,
        datasets: [
          {
            label: "Number Frequency",
            borderWidth: 1,
            backgroundColor: [],
            borderColor: [],
            pointBorderColor: "#2554FF",
            data: null,
          },
        ],
      },
      options: {
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
              },
              gridLines: {
                display: true,
              },
            },
          ],
          xAxes: [
            {
              gridLines: {
                display: false,
              },
            },
          ],
        },
        legend: {
          display: true,
        },
        responsive: true,
        maintainAspectRatio: false,
      },
    };
  },
  methods: {
    sync: function () {
      //Create color rules
      this.chartData.datasets[0].backgroundColor = [];
      this.chartData.datasets[0].borderColor = [];

      for (let i = 0; i < this.info.size; i++) {
        this.chartData.datasets[0].backgroundColor.push(this.color1);
        this.chartData.datasets[0].borderColor.push(this.color2);
      }

      this.chartData.labels = this.info.labels;
      this.chartData.datasets[0].data = this.info.freqArray;
    },
  },
  mounted: function () {
    this.sync();
    this.renderChart(this.chartData, this.options);
  },
};
</script>