<template>
  <div class="home">
    <div class="updateText">
      <h5>{{time}}</h5>
      <v-btn
        color="secondary"
        fab
        x-small
        dark
        class="btn"
        @click="updateCharts"
      >
        <v-icon>mdi-update</v-icon>
      </v-btn>
    </div>

    <v-card class="toolbarPage black--text">
      <v-tabs
        background-color="light-blue lighten-4"
        v-model="tabs"
        centered
        icons-and-text
        style="margin-bottom: 15px"
      >
        <v-tabs-slider></v-tabs-slider>

        <v-tab href="#tab1">
          Bar Chart
          <v-icon>mdi-chart-bar</v-icon>
        </v-tab>

        <v-tab href="#tab2">
          Pie Chart
          <v-icon>mdi-chart-pie</v-icon>
        </v-tab>

        <v-tab href="#tab3">
          Table Chart
          <v-icon>mdi-table</v-icon>
        </v-tab>
      </v-tabs>
    </v-card>

    <v-tabs-items v-model="tabs">
      <v-tab-item value="tab1">
        <div>
          <h3 class="title">Bar Chart</h3>
          <bar-chart :info="data" :update="update" />
        </div>
      </v-tab-item>

      <v-tab-item value="tab2" @click="updateCharts">
        <div>
          <h3 class="title">Pie Chart</h3>
          <pie-chart :info="data" :update="update" />
        </div>
      </v-tab-item>

      <v-tab-item value="tab3" @click="updateCharts">
        <div>
          <h3 class="title">Table Chart</h3>
          <table-chart :info="data" :update="update" />
        </div>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>

import BarChart from "@/components/BarChart";
import PieChart from "@/components/PieChart";
import TableChart from "@/components/TableChart";

import fb from "@/fb";

export default {
  name: "Home",
  components: {
    BarChart,
    PieChart,
    TableChart,
  },
  data: function () {
    return {
      time: null,
      tabs: null,
      update: [],
      data: {
        size: null,
        labels: [],
        freqArray: [],
        freqacArray: [],
        probArray: [],
        probacArray: [],
      },
    };
  },
  watch:{
    tabs: function(){
      this.updateCharts();
    }
  },
  methods: {
    updateCharts: function () {
      console.log(22);
      var da = Date.now();
      var d = new Date(da);
      var months = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
      let time =
        d.getDate() +
        " " +
        months[d.getMonth()] +
        " " +
        d.getFullYear() +
        " - " +
        d.getHours() +
        ":" +
        (d.getMinutes()<10?'0':'') + d.getMinutes() +
        ":" +
        (d.getSeconds()<10?'0':'') + d.getSeconds();

        this.time = "Updated on: " + time;

      this.callFirebase();
    },
    callFirebase: function () {
      var rawData = [];

      this.data.labels = [];
      this.data.freqArray = [];
      this.data.freqacArray = [];
      this.data.probArray = [];
      this.data.probacArray = [];

      fb.ref("/").once("value", (snapshot) => {
        snapshot.forEach(function (childSnapshot) {
          rawData.push(childSnapshot.val());
        });

        for (let i = 5; i <= 40; i++) {
          this.data.labels.push(i); // CALCULATE LABELS
        }

        // INITIALIZATE FREQUENCIES ARRAY
        for (let i = 0; i < this.data.labels.length; i++) {
          this.data.freqArray[i] = 0;
        }

        // CALCULATE FREQUENCIES ARRAY
        for (let i = 0; i < rawData.length; i++) {
          this.data.freqArray[rawData[i] - 5]++;
        }

        // CALCULATE ABSOLUTE FREQUENCIES ARRAY
        this.data.freqacArray[0] = this.data.freqArray[0];

        for (let i = 1; i < this.data.labels.length; i++) {
          this.data.freqacArray[i] =
            this.data.freqacArray[i - 1] + this.data.freqArray[i];
        }

        // CALCULATE PROBABILITIES ARRAY
        for (let i = 0; i < this.data.labels.length; i++) {
          this.data.probArray[i] =
            Math.round(
              (this.data.freqArray[i] /
                this.data.freqacArray[this.data.labels.length - 1]) *
                1000000
            ) / 1000000;
        }

        // CALCULATE ABSOLUTE PROBABILITIES ARRAY
        this.data.probacArray[0] = this.data.probArray[0];

        for (let i = 1; i < this.data.labels.length; i++) {
          this.data.probacArray[i] =
            Math.round(
              (this.data.probacArray[i - 1] + this.data.probArray[i]) * 1000000
            ) / 1000000;
        }

        this.data.size = this.data.labels.length; //CALCULATE SIZE

        this.update.push(true);
      });
    },
  },
  created: function () {
    this.updateCharts();
  },
};
</script>
