<script>
import { Pie } from "vue-chartjs";

export default {
  extends: Pie,
  props: ["info", "update"],
  watch: {
    update: function () {
      if (this.update[this.update.length - 1] == true) {
        this.sync();
        this.renderChart(this.chartData, this.options);
      }
    },
  },
  data() {
    return {
      colors1: [
        "rgba(255, 99, 132, 0.5)",
        "rgba(54, 162, 235, 0.5)",
        "rgba(255, 206, 86, 0.5)",
        "rgba(75, 192, 192, 0.5)",
      ],
      colors2: [
        "rgba(255,99,132,1)",
        "rgba(54, 162, 235, 1)",
        "rgba(255, 206, 86, 1)",
        "rgba(75, 192, 192, 1)",
      ],
      chartData: {
        labels: null,
        datasets: [
          {
            borderWidth: 1,
            borderColor: [],
            backgroundColor: [],
            data: null,
          },
        ],
      },
      options: {
        legend: {
          display: true,
        },
        responsive: true,
        maintainAspectRatio: false,
      },
    };
  },
  methods: {
    sync: function () {
      //Create color rules
      this.chartData.datasets[0].backgroundColor = [];
      this.chartData.datasets[0].borderColor = [];

      for (let i = 0; i < this.info.size; i++) {
        let m = i % 4;
        this.chartData.datasets[0].backgroundColor.push(this.colors1[m]);
        this.chartData.datasets[0].borderColor.push(this.colors2[m]);
      }

      this.chartData.labels = this.info.labels;
      this.chartData.datasets[0].data = this.info.freqArray;
    },
  },
  mounted: function () {
    this.sync();
    this.renderChart(this.chartData, this.options);
  },
};
</script>